.log-entry {
    margin-bottom: 5px;
    padding: 5px;
    background-color: #f0f0f0;
    border-radius: 3px;
    cursor: pointer;
}

.log-entry:hover {
    background-color: #e0e0e0;
}

.log-entry.current-log {
    background-color: #ffffcc;
    font-weight: bold;
}

.log-id {
    font-weight: bold;
    margin-right: 5px;
}